@import '../../utils/variables/style-variables';

.additionalInfo__container {
	width: 100vw;
	height: 40vh;
	display: flex;
	flex: 1;
	.additionalInfo__image {
		width: 30vw;
		height: 100%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}
	.additionalInfo__info {
		width: 40vw;
		display: flex;
		justify-content: center;
		flex-direction: column;
		padding: 0 64px;
		.additionalInfo__heading {
			text-transform: uppercase;
			letter-spacing: 4px;
			font-weight: 700;
			margin-bottom: 24px;
		}
		.additionalInfo__description {
			color: $color-text-secondary;
			font-size: 1.2rem;
			line-height: 2rem;
		}
	}
}
