@import '../../utils/variables/style-variables';

.slideshow__container {
	position: relative;
	display: flex;
	width: 100vw;
	height: 60vh;
	.slideshow__image {
		width: 60vw;
		height: 100%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		background-color: blue;
	}
	.slideshow__info {
		width: 40vw;
		height: 100%;
		background: $color-primary;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 0 80px;
		position: relative;
	}
}
.slideshow__heading {
	font-weight: 600;
	font-size: 4rem;
}
.slideshow__description {
	color: $color-text-secondary;
	font-size: 1.2rem;
	line-height: 2rem;
	margin: 20px 0;
}

.slideshow__shop-btn {
	padding: 16px;
	align-self: flex-start;
	display: flex;
	align-items: center;
	cursor: pointer;

	span {
		font-size: 1.4rem;
		text-transform: uppercase;
		letter-spacing: 4px;
		margin-right: 32px;
		font-weight: 500;
	}
}
.slideshow__buttons {
	display: flex;
	position: absolute;
	left: 0;
	bottom: 0;
	.slideshow__left,
	.slideshow__right {
		width: 5vw;
		height: 5vw;
		background: $color-secondary;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		&:hover {
			background: $color-text-primary;
		}
	}
}
