@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@200;300;400;500;600;700&display=swap);
:root {
	font-size: 12px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Spartan', 'Roboto', 'Ubuntu', sans-serif;
}

.arrow{background:#000;border-radius:99px}.arrow::after,.arrow::before{content:'';display:block;width:3px;border-radius:99px;height:12px;background:#000;position:relative;left:36px}.arrow::after{top:-13px;-webkit-transform:rotateZ(45deg);transform:rotateZ(45deg)}.arrow::before{top:-8px;-webkit-transform:rotateZ(-45deg);transform:rotateZ(-45deg)}.arrow-thin{height:3px}.arrow-normal{height:4px}.arrow-normal::after,.arrow-normal::before{width:4px}.arrow-normal::before{top:-7px}.arrow-thick{height:5px}.arrow-thick::after,.arrow-thick::before{width:5px}.arrow-thick::before{top:-6px}.arrow-w24{width:24px}.arrow-w24::after,.arrow-w24::before{left:20px}.arrow-w32{width:32px}.arrow-w32::after,.arrow-w32::before{left:28px}.arrow-w40{width:40px}.arrow-w40::after,.arrow-w40::before{left:36px}.arrow-w48{width:48px}.arrow-w48::after,.arrow-w48::before{left:44px}.arrow-w56{width:56px}.arrow-w56::after,.arrow-w56::before{left:52px}.arrow-w64{width:64px}.arrow-w64::after,.arrow-w64::before{left:60px}.arrow-up{-webkit-transform:rotateZ(-90deg);transform:rotateZ(-90deg)}.arrow-down{-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg)}.arrow-left{-webkit-transform:rotateZ(180deg);transform:rotateZ(180deg)}

.slideshow__container{position:relative;display:flex;width:100vw;height:60vh}.slideshow__container .slideshow__image{width:60vw;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover;background-color:blue}.slideshow__container .slideshow__info{width:40vw;height:100%;background:#fff;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:0 80px;position:relative}.slideshow__heading{font-weight:600;font-size:4rem}.slideshow__description{color:#a1a1a1;font-size:1.2rem;line-height:2rem;margin:20px 0}.slideshow__shop-btn{padding:16px;align-self:flex-start;display:flex;align-items:center;cursor:pointer}.slideshow__shop-btn span{font-size:1.4rem;text-transform:uppercase;letter-spacing:4px;margin-right:32px;font-weight:500}.slideshow__buttons{display:flex;position:absolute;left:0;bottom:0}.slideshow__buttons .slideshow__left,.slideshow__buttons .slideshow__right{width:5vw;height:5vw;background:#000;cursor:pointer;display:flex;align-items:center;justify-content:center}.slideshow__buttons .slideshow__left:hover,.slideshow__buttons .slideshow__right:hover{background:#454545}

.additionalInfo__container{width:100vw;height:40vh;display:flex;flex:1 1}.additionalInfo__container .additionalInfo__image{width:30vw;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover}.additionalInfo__container .additionalInfo__info{width:40vw;display:flex;justify-content:center;flex-direction:column;padding:0 64px}.additionalInfo__container .additionalInfo__info .additionalInfo__heading{text-transform:uppercase;letter-spacing:4px;font-weight:700;margin-bottom:24px}.additionalInfo__container .additionalInfo__info .additionalInfo__description{color:#a1a1a1;font-size:1.2rem;line-height:2rem}

